<template>
  <div>
    <app-modal-nutrition-label></app-modal-nutrition-label>
    <app-search></app-search>
    <app-header></app-header>
    <app-filters></app-filters>
    <app-items></app-items>
    <app-footer></app-footer>
  </div>
</template>

<script>
  import { eventService } from "./main";
  import NutritionLabel from './components/modals/NutritionLabel.vue';
  import Search from './components/Search.vue';
  import Header from './components/layout/Header.vue';
  import Filters from "./components/Filters.vue";
  import Items from "./components/Items.vue";
  import Footer from './components/layout/Footer.vue';

  export default {
    components: {
      'app-modal-nutrition-label' : NutritionLabel,
      'app-search' : Search,
      'app-header' : Header,
      'app-filters' : Filters,
      'app-items' : Items,
      'app-footer': Footer
    },
    created(){
      // Little Caesars Pizza
      this.getData(76);
    },
    methods: {
      getData(id){
        this.$http.get('?brandID='+ id )
        .then(response => {
          return response.json();
        },
        error => console.log(error))
        .then(data => {
          eventService.$emit('onDataLoad', data);
        });
      }
    }
  }
</script>

<style lang="scss">
  body {
    background-color: #E3F2FD;
  }
</style>



