<template>
  <div class="header">
  	<b-container>
	    <b-row>
	      <b-col>
	        <h1 :class="animate">{{ brandName }}</h1>
	        <h2 :class="animate">Nutrition Portal</h2>
	        <button class="btn btn-search"
                  :class="animate"
                  @click="toggleSearch">
	          <icon name="search"></icon>
	        </button>
	      </b-col>
	    </b-row>
    </b-container>
  </div>
</template>

<script>
  import { eventService } from "../../main";

	export default {
    data() {
      return {
        isDataLoaded: false,
        brandName: 'Nixy\'s Cafe',
        btnToggleSearch: false
      }
    },
    created(){
      eventService.$on('onDataLoad', (data) => {
        this.brandName = data.generalInfo.brandName;
        this.isDataLoaded = true;
      });
    },
    methods: {
      toggleSearch() {
        this.btnToggleSearch = !this.btnToggleSearch;
        eventService.$emit('toggleSearch', this.btnToggleSearch);
      }
    },
    computed: {
      animate(){
        return {
          'animate': this.isDataLoaded
        }
      }
    }
	}
</script>

<style lang="scss">
	.header {
		padding: 15px 0;
	  background-color: #fff;
	  h1 {
	    font-size: 24px;
	    @media(min-width: 768px) {
	    	font-size: 36px;
	    }
	  }
	  h2 {
	    font-size: 14px;
	    color: #999;
	    text-transform: uppercase;
	    font-weight: normal;
	    letter-spacing: 2px;
	    @media(min-width: 768px) {
	    	font-size: 18px;
	    }
	  }
	  .btn-search {
	    position:absolute;
	    right: 15px;
	    top:50%;
	    margin-top: -15px;
	  }
    h1, h2, .btn-search {
      transition: all 600ms;
      opacity: 0;
      &.animate {
        opacity: 1;
      }
    }
	}
</style>



