<template>
  <b-container>
    <b-row>
      <b-col>
				<div class="loader" :class="animateSpinner">
					<icon name="spinner" pulse></icon>
				</div>

        <transition name="slide" type="animation">
          <b-alert variant="info"
                   class="some-alert"
                   :show="isFiltered && noMatch"
                   v-if="isFiltered && noMatch">
            <p>Your search "<strong>{{ q }}</strong>" did not match any items.</p>
            <h6>Suggestions</h6>
            <ul>
              <li>Make sure all words are spelled correctly.</li>
              <li>Try different keywords.</li>
              <li>Try more general keywords.</li>
            </ul>
          </b-alert>
        </transition>

<!-- menu group -->
        <ul class="list-group list-group-menu" :class="animateMenu" v-no-match>
          <li class="list-group-item list-group-item-action"
          	  v-b-toggle="'category-' + index"
          	  v-for="(menu, index) in menus"
          	  v-show="buildMenu(menu.taggedData).length > 0">
          		<div class="list-item-content">
	            	<strong v-html="highlight(menu.name)"></strong>
	            	<small>
	            		{{ buildMenu(menu.taggedData).length }}
	            		<template v-if="isFiltered">matching</template>
	            		item<template v-if="buildMenu(menu.taggedData).length > 1">s</template>
	            	</small>
	            	<icon name="chevron-right" class="arrow arrow-right"></icon>
	            	<icon name="chevron-down" class="arrow arrow-down"></icon>
            	</div>
<!-- group items -->
	            <b-collapse :id="'category-' + index">
	            	<ul class="list-group list-group-menu-items">
		            	<li class="list-group-item list-group-item-action"
		            			v-b-modal="'label'"
		            			@click.stop="showNutritionInfo(item)"
		            	    v-for="item in buildMenu(menu.taggedData)">
		            		<div class="item-name" v-html="highlight(item.name)"></div>
		            		<span class="cal">{{ item.calories }}</span>
		            		<icon name="chevron-right" class="arrow"></icon>
		            	</li>
	            	</ul>
	          	</b-collapse>
          </li>
        </ul>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
  import { eventService } from "../main";

	export default {
		data(){
			return {
        filters: [],
				menus: {},
				loading: false,
        q: '',
        noMatch: false
			}
		},
    directives: {
      noMatch: {
        componentUpdated(el, binding, vnode){
          let hiddenNodes = Array.from(el.querySelectorAll(".list-group-menu > li[style*='display: none']"));
          vnode.context.noMatch = el.childNodes.length === hiddenNodes.length ? true : false;
        }
      }
    },
		created() {
      // some delay for spinner fade-in effect.
      setTimeout(()=>{
        this.loading = true
      }, 10);

      eventService.$on('onDataLoad', (data) => {
        this.menus = data.inm;
        this.loading = false;
      });

      eventService.$on('onSearch', (q) => {
        this.q = q;
      });

      eventService.$on('onSelectFilters', (filters) => {
        this.filters = filters;
      });
		},
		methods: {
			showNutritionInfo(item) {
        eventService.$emit('onItemClick', item);
			},
      // return item allergens
      pickAllergens(item){
        let picked = [],
            allergens = (({ allergen_contains_alcohol, allergen_contains_artificial_coloring, allergen_contains_corn,
                            allergen_contains_eggs, allergen_contains_fish, allergen_contains_garlic, allergen_contains_gluten,
                            allergen_contains_milk, allergen_contains_msg, allergen_contains_nitrates, allergen_contains_onion,
                            allergen_contains_peanuts, allergen_contains_seeds, allergen_contains_shellfish, allergen_contains_soy,
                            allergen_contains_meat, allergen_contains_animal_products, allergen_contains_sulfites, allergen_contains_tree_nuts,
                            allergen_contains_wheat, allergen_contains_yeast, allergen_contains_crustacean_shellfish,
                            allergen_contains_molluscan_shellfish,allergen_contains_cross_contact
                          }) => ({ allergen_contains_alcohol, allergen_contains_artificial_coloring, allergen_contains_corn,
                                   allergen_contains_eggs, allergen_contains_fish, allergen_contains_garlic, allergen_contains_gluten,
                                   allergen_contains_milk, allergen_contains_msg, allergen_contains_nitrates, allergen_contains_onion,
                                   allergen_contains_peanuts, allergen_contains_seeds, allergen_contains_shellfish, allergen_contains_soy,
                                   allergen_contains_meat, allergen_contains_animal_products, allergen_contains_sulfites, allergen_contains_tree_nuts,
                                   allergen_contains_wheat, allergen_contains_yeast, allergen_contains_crustacean_shellfish,
                                   allergen_contains_molluscan_shellfish,allergen_contains_cross_contact
                                 }))(item);

        for(let key in allergens) {
          if(allergens[key] == 1) {
            picked.push(key.substring('allergen_contains_'.length));
          }
        }

        return picked;
      },
			// show items based on user dietary preference and search string
			buildMenu(items){
				let itemAllergens = [],
            _that = this;

				return items.filter((item) => {
          itemAllergens = _that.filters.filter((n) => _that.pickAllergens(item).includes(n));

          if(itemAllergens.length == 0 && item.name.toLowerCase().indexOf((_that.q).toLowerCase()) != -1) {
            return true;
          }
				});
			},
      highlight(text) {
        if(this.q == '') { return text }

        let check = new RegExp(this.q, "ig");
        return text.toString().replace(check, (matched) => {
            return ('<span class="highlight">' + matched + '</span>');
        });
      }
		},
		computed: {
			isFiltered() {
				return this.q || this.filters.length > 0  ? true : false;
			},
			animateMenu() {
				return {
					'animate': !this.loading
				}
			},
      animateSpinner() {
        return {
          'fade-in': this.loading,
          'fade-out': !this.loading
        }
      }
		}
	}
</script>

<style lang="scss">

	.list-group-menu {
		margin-top: 40px;
		opacity: 0;
		transition: all 600ms;
		&.animate {
			margin-top: 0;
			opacity: 1;
		}
	  >.list-group-item {
	    line-height: normal;
	    padding: 0;
	    overflow: hidden;
	    strong {
	      display: block;
	    }
	    .arrow {
	      position: absolute;
	      right: 15px;
	      top: 29px;
	      margin-top: -7px;
	    }
	    .arrow-right {
	    	display: none;
	    }
	    .arrow-down {
	    	display: block;
	    }
	    .cal {
	    	position: absolute;
	    	right: 40px;
	    	top: 50%;
	    	text-align: right;
	    	margin-top: -10px;
	    	&:after {
	    		content: ' cal';
	    		font-size: 12px;
	    		color: rgba(0,0,0,.4);
	    	}
	    }
	    &.collapsed {
	    	.arrow-right {
	    		display: block;
	    	}
	    	.arrow-down {
	    		display: none;
	    	}
	    }
	    &:active {
	    	background-color: #f8f9fa;
	    	color: #495057;
	    }
	  }
	}
	.list-group-menu-items {
		margin: 0;
		.list-group-item {
			border-left: none;
			border-right: none;
			border-bottom: none;
			padding-left: 30px;
			&:active {
				background-color: #FFF8E1;
			}
			&:first-child {
				border-radius: 0;
			}
			.arrow {
				top: 50%;
				margin-top: -7px;
			}
		}
	}
	.list-item-content {
		padding: 10px 20px;
	}
	.list-group-item {
		border-color: #E3F2FD;
	}
	.arrow {
		color: #B0BEC5;
	}
	.multiselect__tag {
		margin-bottom: 3px;
	}
	.item-name {
		margin-right: 85px;
	}
	.loader {
		position: absolute;
		left: 50%;
		top: 10px;
		margin-left: -4px;
		z-index: 100;
    transition: all 400ms;
    opacity: 0;
    &.fade {
      &-in {
        opacity: 1;
      }
      &-out {
        opacity: 0;
      }
    }
	}
  .highlight {
    background-color: #FFFF73;
  }

  .some-alert{
    position: absolute;
    z-index: 100;
    width: 90%;
    left: 50%;
    padding-top: 20px;
    margin-left: -45%;
    ul {
      margin-left: 20px;
      padding-left: 0;
    }
    @media(min-width: 768px) {
      width: 500px;
      left: 50%;
      margin-left: -250px;
    }
  }

  .slide {
    &-enter {
      opacity: 0;
    }
    &-enter-active {
      animation: slide-in 400ms forwards;
      transition: opacity 300ms;
    }
    &-leave-active {
     animation: slide-out 400ms forwards;
     transition: opacity 300ms;
     opacity: 0;
    }
  }

  @keyframes slide-in {
    0% {
      transform: translateY(-30px);
    }
    100% {
      transform: translateY(0);
    }
  }

  @keyframes slide-out {
    0% {
      transform: translateY(0);
    }
    100% {
      transform: translateY(30px);
    }
  }


</style>



