<template>
  <div class="search-bar" :class="animate">
    <b-container>
      <b-row>
        <b-col md="5" offset-md="7">
          <input type="text"
                 placeholder="Search"
                 class="form-control"
                 v-model="q"
                 v-focus="isOpen"
                 @keyup="search">
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>
<script>
  import { eventService } from "../main";

  export default {
    data () {
      return {
        isOpen: false,
        q: ''
      }
    },
    created() {
      eventService.$on('toggleSearch', (toggleState) => {
          this.isOpen = toggleState;

          if(!this.isOpen) {
            this.reset();
          }
      });
    },
    methods: {
      search() {
        eventService.$emit('onSearch', this.q);
      },
      reset(){
        this.q = '';
        this.search();
      }
    },
    computed: {
      animate() {
        return {
          'animate': this.isOpen
        }
      }
    }
  }
</script>
<style lang="scss">
  .search-bar {
    padding: 8px 0;
    background-color: #333;
    z-index: 100;
    transition: all 300ms;
    margin-top: -60px;
    &.animate {
      margin-top: 0;
    }
  }
</style>



